import React, { Suspense } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import Loading from './components/Loading/Loading';
import { fadeOut } from 'react-animations'
import Radium from 'radium';
function App() {
	//						<Route path='/' exact component={Home} />
	//const Home = React.lazy(() => import('./components/Home/Hiatus/HiatusHome').then(({Home}) => ({default: Home}))
	//const Home = React.lazy(() =>import("./components/Home/index"));
	const Home = React.lazy(() => {
		return new Promise(resolve => setTimeout(resolve, 2500)).then(() => import("./components/Home/index"));
	});


	const styles: any = {
		fadeOut: {
			animation: '3s',
			animationName: Radium.keyframes(fadeOut, 'fade'),
		}
	}
	return (
		
		<div className="App">
			<Suspense fallback={<Loading />}>
				<Home />
			</Suspense>
		</div>

	)
}

export default App;
